
import XHeader from 'vux/src/components/x-header/index.vue'

import NoData from '../../components/noData'


export default {
name: "SelectOrder",
components: {
XHeader,
NoData
},
data() {
return {
id: '',
list: []
}
},
mounted() {
this.id = this.$route.query.id
this.getData()
},
methods: {
getData() {
axios.get(`/Cost/GetPagedCrmReturnedMoney?customerId=${this.id}`).then(res => {
if (res.data.State === 1) {
this.list = res.data.Data.List
}
}).catch(e => {

})
},
selectOrder(item) {
this.$bus.emit('selectOrder', item)
this.$router.back()
}
}
}



